import { Address } from './Address';
import { Company } from './Company';

class Author {
    constructor({ id, name, username, email, phone, address, company }) {
        this.id = id;
        this.name = name;
        this.username = username;
        this.email = email;
        this.phone = phone;
        this.address = new Address(address);
        this.company = new Company(company);
    }
}

export { Author };


